import React, { createContext, useState, useContext, useEffect } from 'react';
import { getProfileDashboard, initializeTelegramAuth } from '../services/api';
import { getTelegramUserData } from '../utils/telegramUtils';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [token, setToken] = useState(localStorage.getItem('token'));

  useEffect(() => {
    const fetchUser = async () => {
      try {
        if (token) {
          const userData = await getProfileDashboard();
          setUser(userData);
        } else {
          const isTelegram = !!window.Telegram?.WebApp;
          if (isTelegram) {
            const telegramData = await getTelegramUserData();
            if (telegramData) {
              const authResponse = await initializeTelegramAuth(telegramData);
              setToken(authResponse.token);
              localStorage.setItem('token', authResponse.token);
              setUser(authResponse.user);
            }
          }
        }
      } catch (error) {
        console.error('Failed to fetch user data:', error);
        // Clear token if it's invalid
        if (error.response && error.response.status === 401) {
          setToken(null);
          localStorage.removeItem('token');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [token]);

  const updateUser = (userData) => {
    setUser(prevUser => ({ ...prevUser, ...userData }));
  };

  const logout = () => {
    setUser(null);
    setToken(null);
    localStorage.removeItem('token');
  };

  return (
    <AuthContext.Provider value={{ user, setUser, updateUser, loading, token, setToken, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);